const Term = () => {
  const [fileContent, setFileContent] = useState('');

  useEffect(() => {
    // Cargar el contenido del archivo aqu (puedes usar Fetch, Axios, etc.)
    fetch('/data/terminos.md') // Utiliza la variable que contiene la ruta al archivo
      .then(response => response.text())
      .then(data => setFileContent(data))
      .catch(error => console.error('Error cargando el archivo:', error));
  }, []); 
  return (
    <div className="container mx-auto flex flex-col md:flex-row">
    <MarkdownViewer markdownContent={fileContent} /> 
    <SidebarRight/>
     
    </div>
  )
 }
function App() { 
  return (
    <BrowserRouter>
      <Routes> 
            <Route index element={<Home />} /> 
            <Route path="terminos" element={<Term />} /> 
       </Routes>
    </BrowserRouter>
  );
}

export default App;
